@import '~styles/utils.scss';

.LandingLine {
    display: flex;
    align-items: center;

    height: 200px;

    justify-content: center;

    background: $oc-gray-9;

    @include media('<1300px') {
        display: block;
        overflow: scroll;
        justify-content: center;
        white-space: nowrap;
        flex-wrap: wrap;
    }
}
